.CodeEditorParent {
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
    
    height: 100%;
}

.prompt {
    border-radius: 0.4rem;
    background-color: rgb(250,250,250);
    outline-style: none;
    
}


.CodeEditorHeader {
    display: flex;
    justify-items: center;
    gap: 0.6rem;
    
    height: 2.5rem;
    align-items: center;
    

    background-color: rgb(250, 250, 250);

}

.EditorAndConsole {
    display: flex;
    flex-direction: column;


    height: 100%;
}

.CodeEditor {
    height: 100%;
    border: solid 0px rgb(202, 190, 190);
    text-align: left;
}

.CodeEditor > .cm-theme {
    height: 100%;
}

.Console {
    height: 100%;
    border: solid 0px rgb(202, 190, 190);
    text-align: left;
}

    .Console > .cm-theme {
        height: 100%;
    }

.editor-description {
    font-family: Roboto, Helvetica, Arial, sans-serif;
    color: rgba(0, 0, 0, 0.6);
    font-size: 14px;

    cursor: default
}

@media only screen and (max-width: 600px) {
    .CodeEditor {
        height: 35rem;
    }

    .prompt {
        min-height: 3rem;
    }
  }