.page {
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
    
    height: 100%;
    
}

    .page-header {
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        font-size: 1.5rem;
        font-family: "Roboto, Helvetica, Arial, sans-serif";
        color: rgba(0, 0, 0, 0.6);

        padding: 0.2rem;
        height: 2rem;
        cursor: default;

        background-color: rgb(250,250,250);
    }

.measurement-gen {
    display: flex;
    gap: 0.6rem;

    height: 100%;
    overflow: auto;
}

.input-space {
    display: flex;
    flex-direction: column;

    width: calc(50%);
    min-width: 360px;
    height: 100%;
}

    .circuit {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        height: calc(50%);
        border-radius: 0.4rem;
        
        background-color: rgb(197,222,221);
    }

    .page-decription {
        font-family: Roboto, Helvetica, Arial, sans-serif;
        color: rgba(0, 0, 0, 0.6);
        font-size: 14px;

        padding-left: 0.6rem;
        cursor: default
    }

.code-editor {
    display: block;

    width: calc(50%);
    min-width: 360px;
    height: 100%;
    border-radius: 0.4rem;
    
    background-color: rgb(250,250,250);
}


@media only screen and (max-width: 600px) {
    .measurement-gen {
        display: flex;
        flex-direction: column;

        align-items: center;
    
        overflow: auto;
    }

    .code-editor {
        height: 35rem;
    }
  }