.text-pagination {
    display: grid;
    grid-template-columns: repeat(4, minmax(30px, 1fr));
    padding-left: 0.2rem;
}

.text-pagination-element {
    display: flex;
    align-items: center;
    justify-content: center;

    font-family: Roboto, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    border-radius: 0.4rem;

    cursor: default;
}

.text-pagination-element-selected {
    background-color: rgb(197,222,221)
}

.text-pagination-element-unselected {

}

.text-pagination-element-unselected:hover {
    cursor: pointer;
    text-decoration: underline;
}

@media only screen and (max-width: 600px) {
    .text-pagination {
        grid-template-columns: repeat(2, minmax(30px, 1fr));
    }
  }